<template>
    <header class="header-global">
        <base-nav class="navbar-main" transparent type="" effect="light" expand>
            <router-link slot="brand" class="navbar-brand mr-lg-5" to="/">
                <img src="@/assets/image/dood.png" alt="logo">
            </router-link>


            <div class="row" slot="content-header" slot-scope="{closeMenu}">
                <div class="col-6 collapse-brand">
                    
                    <img src="@/assets/image/dood.png">
                   
                </div>
                <div class="col-6 collapse-close">
                    <close-button @click="closeMenu"></close-button>
                </div>
            </div>

            <ul class="navbar-nav navbar-nav-menu navbar-nav-hover align-items-lg-center">
                <!--<router-link to="/"><div  tag="li" class="nav-item-menu">About</div></router-link>
                <router-link to="/passport"><div  tag="li" class="nav-item-menu">Passport</div></router-link>-->



                
                <div class="connect-btn2" v-if="!!account"><span @click="connect()">{{ subAddress(account) }}</span></div>
                
                <div class="connect-btn2" v-else><span @click="connect()" >Connect Wallet</span></div>
            </ul>
            <ul class="navbar-nav align-items-lg-center  ml-lg-auto">
                <li class="nav-item d-none d-lg-block ml-lg-4 connect-btn" v-if="!!account">
                    
                        <span class="btn-inner--icon">
                          <i class="fa fa-wallet "></i>
                        </span>
                        <span class="nav-link-inner--text">{{ subAddress(account) }}</span>

                </li>

                <li class="nav-item d-none d-lg-block ml-lg-4 connect-btn"  @click="connect()" v-else>
                    
                        <span class="btn-inner--icon">
                          <i class="fa fa-wallet "></i>
                        </span>
                        <span class="nav-link-inner--text">Connect Wallet</span>

                </li> 
            </ul>
        </base-nav>
    </header>
</template>
<script>
import BaseNav from "@/components/BaseNav";
import BaseDropdown from "@/components/BaseDropdown";
import CloseButton from "@/components/CloseButton";

import { mapActions } from 'vuex'

const Web3 = require('web3')

export default {
  components: {
    BaseNav,
    CloseButton,
    BaseDropdown
  },
  async mounted() {
    //await this.connect()
  },
  computed: {
    account() {
        return this.$store.state.web3.account
    }
  },
  methods: {
    ...mapActions(['updateAccount']),
    async connect() {
        console.log('connect')
        const self = this
        if (window.ethereum) {
            //await window.ethereum.enable()
            var accounts = await window.ethereum.request({ method: "eth_requestAccounts" })
            window.web3 = new Web3(window.ethereum);
            
            const account = accounts.length > 0 ? accounts[0] : null
            console.log(account)

            //self.$store.commit('UPDATE_ACCOUNT', account)
            this.updateAccount(account)

            console.log(self.$store.state.account)
            return true
        }
        return false
    },
    subAddress(address) {
        return address.slice(0, 6) + '...' + address.substr(-6, 6)
    }
  }
};
</script>
